import React, { useEffect, useState, useRef } from "react";
import axios from "axios";
import moment from "moment";
import { Box, List, CircularProgress, Tooltip } from "@material-ui/core";
import { useSnackbar } from "notistack";
import LoadingButton from "../../../reviewer/common/LoadingButton";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";

import { MAX_ALLOWED_SIZE } from "../../../../config";
import {
  ProductDocument,
  ProductDocumentType,
  Document,
} from "../../../reviewer/common/types";

export interface UploadCertOrDisclosureBtnProps {
  manufacturerId: number;
}

export default function UploadCertOrDisclosureBtn({
  manufacturerId,
}: UploadCertOrDisclosureBtnProps) {
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState<boolean>(true);
  const [doc, setDoc] = useState<ProductDocument | null>(null);
  const inputRef = useRef(null);
  const updateInputRef = useRef(null);
  const documentCopy = (document && Object.assign({}, document)) || null;
  // const [file, setFile] = useState<Document | null>(documentCopy);
  const [anchorEl, setAnchorEl] = React.useState(null);

  useEffect(() => {
    axios
      .post(`/api/client/manufacturer/${manufacturerId}/documents`)
      .then(async (response) => {
        setLoading(false);
        if (response.status == 200 || response.status == 201) {
          console.log(response.data);
          setDoc(response.data[0]);
        } else {
          console.log(response);
          enqueueSnackbar("Failed to retrieve manufacturer spec sheet.", {
            variant: "error",
          });
        }
      })
      .catch((e) => {
        console.error(e);
        setLoading(false);
        enqueueSnackbar("Failed to retrieve manufacturer spec sheet.", {
          variant: "error",
        });
      });
  }, []);

  const handleClick = (event: any) => {
    setAnchorEl(event.currentTarget);
  };

  const handleDocumentUploadButton = () => {
    // @ts-ignore
    if (inputRef) inputRef.current.click();
  };

  const handleDocumentUpdateButton = () => {
    // @ts-ignore
    if (inputRef) updateInputRef.current.click();
  };

  // const setDocumentHandler = (document: Document) => {
  //   setDoc(document as ProductDocument);
  //   console.log(doc);
  // };

  // const updateDocHandler = (doc: Document, documentId: number) => {
  //   const formData = new FormData();
  //   formData.append("document", doc);

  //   return axios.post(`/api/client/manufacturer/document/${documentId}`, formData, {
  //     headers: {
  //       "Content-Type": "multipart/form-data",
  //     },
  //   });
  // };

  // const deleteDocHandler = (documentId: number) => {
  //   setDoc(doc.filter((d) => d.id !== documentId));

  //   return axios.delete("/api/client/manufacturer/document/" + documentId);
  // };

  const handleDocumentUpload = (e: any) => {
    const _doc = e.target.files[0] as Document;

    if (!_doc) return;

    if (_doc.size > MAX_ALLOWED_SIZE) {
      alert("File exceeds the maximum allowed size of 10 MB.");
      return;
    }

    setLoading(true);
    const formData = new FormData();
    formData.append("document", _doc);
    formData.append("type", ProductDocumentType.SPEC_SHEETS.toString());

    axios
      .post(`/api/client/manufacturer/${manufacturerId}/document`, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then(async (response) => {
        setLoading(false);
        if (response.status == 200 || response.status == 201) {
          // console.log(response.data);
          // setDoc(response.data);
          // setDocument(response.data);
          setDoc(response.data);
          enqueueSnackbar(`Uploaded manufacturer spec successfully.`, {
            variant: "success",
          });
        } else {
          console.log(response);
          enqueueSnackbar(`Product spec sheet upload failed.`, {
            variant: "error",
          });
        }
      })
      .catch((e) => {
        console.error(e);
        setLoading(false);
        enqueueSnackbar(`Product spec sheet upload failed.`, {
          variant: "error",
        });
      });
  };

  if (loading) return <CircularProgress />;

  return (
    <>
      <input
        ref={inputRef}
        type="file"
        name="file"
        id={Math.random().toString()}
        accept="application/msword, application/pdf"
        data-title="Upload"
        // multiple
        // data-multiple-caption="{count} files selected"
        onChange={handleDocumentUpload}
        style={{ display: "none" }}
      />
      {/* <input
        ref={updateInputRef}
        type="file"
        name="updatedFile"
        id={Math.random().toString()}
        accept="application/msword, application/pdf"
        data-title="Upload"
        onChange={handleUpdateDocument}
        style={{ display: "none" }}
      /> */}
      <Tooltip title="A manufacturer specification sheet is a technical document that outlines the features of a manufacturer in detail. It explains and describes the manufacturer, what it is, what it does, what its components are and any special features it has. It also provides any other important additional information such as hazard and allergy warnings.">
        <LoadingButton
          loading={loading}
          done={doc != null}
          onClick={handleDocumentUploadButton}
          startIcon={<CloudUploadIcon />}
          variant="contained"
        >
          Halal Certificate / Disclosure Statement
        </LoadingButton>
      </Tooltip>
    </>
  );
}
